@import url('https://fonts.googleapis.com/css2?family=Edu+NSW+ACT+Foundation:wght@600&display=swap');
*{
  margin: 0;
  padding: 0;
}
body {
  background-color: rgb(115, 235, 155);
  margin: 0;
  font-family: 'Edu NSW ACT Foundation', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Edu NSW ACT Foundation', cursive;
}
#root{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 150px;
  
}
.text{
  border-radius: 9px;
  border: 7px solid seashell;
  /* background-color: rgb(244, 151, 243); */
  background-color: bisque;
}
.top{
  display: flex;
  flex-direction: row;
}
.cartoon img
{
  width: 100px;
  height: 100px;
  margin-right: 30px;
}
.time
{
  display: flex;
  flex-direction: row;
}
.count{
  border: 1px solid grey;
  padding: 10px;
  background-color: whitesmoke;
  margin: 5px;
  height: 20px;
  width: 20px;
  margin-top: 30px;
}
.footer{
  position: absolute;
  bottom: 3%;
  color: grey;
}
.time p{
  margin-top: 40px;
}